import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { addWords } from 'redux/wordsSlice';
import { nanoid } from 'nanoid';
import { Notify } from 'notiflix/build/notiflix-notify-aio';
import AddWordsFormStyled from './AddWordsForm.styled';

const AddWordsForm = () => {
  const words = useSelector(state => state.words);
  const [word, setWord] = useState('');
  const [transletedWord, setTransletedWord] = useState('');
  const dispatch = useDispatch();

  const handleChange = e => {
    switch (e.currentTarget.name) {
      case 'word':
        setWord(e.currentTarget.value);
        break;
      case 'translatedWord':
        setTransletedWord(e.currentTarget.value);
        break;
      default:
        return;
    }
  };

  const resetForm = () => {
    setWord('');
    setTransletedWord('');
  };

  const handleSubmit = e => {
    e.preventDefault();
    if (word === '' || transletedWord === '') {
      Notify.failure('  ');
      return;
    }
    if (
      words.filter(
        item =>
          item.word.toLowerCase() === word.toLowerCase() ||
          item.transletedWord.toLowerCase() === transletedWord.toLowerCase()
      ).length > 0
    ) {
      Notify.warning('     ');
      return;
    }
    dispatch(
      addWords({ word: word, transletedWord: transletedWord, id: nanoid() })
    );
    resetForm();
    Notify.success('  ');
  };

  return (
    <AddWordsFormStyled>
      <h2>      </h2>
      <form onSubmit={handleSubmit}>
        <label>
          <p>  </p>
          <input type="text" name="word" onChange={handleChange} value={word} />
        </label>
        <label>
          <p> </p>
          <input
            type="text"
            name="translatedWord"
            onChange={handleChange}
            value={transletedWord}
          />
        </label>
        <button type="submit"> </button>
      </form>
    </AddWordsFormStyled>
  );
};

export default AddWordsForm;
